    <div class="page-header">
      <div class="title">
        <p>{{productID.goodsName}}</p>
        <div class="list">
          <ul>
            <li>
              <router-link to></router-link>
            </li>
            <li>
              <router-link to> </router-link>
            </li>
            <li>
              <router-link to> </router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <!-- 头部END -->

    <!-- 主要内容 -->
    <div class="main">
      <!-- 左侧商品轮播图 -->
      <div class="block">
        <div height="560px">
          <img width="95%" height="95%"
            :src="product.goodsCoverImg"
            :alt="商品图"
          />
        </div>
 
      </div>
      <!-- 左侧商品轮播图END -->

      <!-- 右侧内容区 -->
      <div class="content">
        <h1 class="name">{{product.goodsName}}</h1>
             <p class="store">{{product.storeName}}</p>
        <p class="intro">{{product.goodsIntro}}</p>
   
        <!-- <div class="price">
          <span>{{product.sellingPrice}}元</span>
          <span
            v-show="product.originalPrice != product.sellingPrice"
            class="del"
          >{{product.originalPrice}}元</span>
        </div> -->
        <div class="pro-list">
          <span class="pro-name">{{product.goodsName}}</span>
          <span class="pro-price">
            <span
              v-show="product.originalPrice != product.sellingPrice"
              class="pro-del"
            > 原价：{{product.originalPrice}}元</span>
          </span>
          <p class="price-sum"> 现价：{{product.sellingPrice}}元</p>
        </div>
        <!-- 内容区底部按钮 -->
        <div class="button">
          <el-button class="shop-cart" :disabled="dis" @click="addShoppingCart">加入购物车</el-button>
          <el-button class="like" @click="addCollect">喜欢</el-button>
        </div>
        <!-- 内容区底部按钮END -->
        <div class="pro-policy">
          <ul>
            <li>
