            </li>
            <li>
              <i class="el-icon-circle-check"></i>  信用卡支付
            </li>
            <li>
              <i class="el-icon-circle-check"></i> 集分宝
            </li>
            <li>
              <i class="el-icon-circle-check"></i> 微信
            </li>
          </ul>
        </div>
      </div>
      <!-- 右侧内容区END -->





    </div>
    <!-- 主要内容END -->
           <div>
