<script>
import { mapActions } from "vuex";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      dis: false, // 控制“加入购物车按钮是否可用”
      productID: "", // 商品id
      productPicture: "", // 商品图片
      product:"",
      comments:"",
    };
  },
  // 通过路由获取商品id
  activated() {
          console.log( "通过路由获取商品id");

    if (this.$route.query.productID != undefined) {
      this.productID = this.$route.query.productID;
      console.log(this.productID);
    }
  },
  watch: {
    // 监听商品id的变化，请求后端获取商品数据
    productID: function(val) {
          console.log( "监听商品id的变化"),
      this.showComment();
      this.getDetails(val);
      this.getDetailsPicture(val);
    }
  },
  created(){
    
  },
  methods: {
    ...mapActions(["unshiftShoppingCart","addcounr","productID","setdetil","getShoppingCart","getNum", "addShoppingCartNum","unshiftCollent"]),
    ...mapGetters([
      "checkShoppingCart",
    ]),
    // 获取商品详细信息
    getDetails(val) {
        this.$axios(   
            {
              url:this.$target+"/goodById", 
              // url:  "http://localhost:3000/detil",
              method: 'get',
              params:{
              goodsId:val
              }
        })
        .then(res => {
          this.product = res.data.data;
          console.log(" this.product", this.product);
         })
        .catch(err => {
          return Promise.reject(err);
        }); 
    },
    // 获取商品图片
    getDetailsPicture(val) {      console.log(val);

      // this.$axios
      //   .post("/api/product/getDetailsPicture", {
      //     productID: val
      //   })
      //   .then(res => {
      //     this.productPicture = res.data.ProductPicture;
      //   })
      //   .catch(err => {
      //     return Promise.reject(err);
      //   });
    },
    showComment(){
        this.$axios( 
        { 
          url:this.$target+"/comment",
          // url:" http://localhost:3000/posts",
          method: 'get',
          params: {
               goodsId: this.productID   
               }
      })
        .then(res => {
          this.comments =res.data.data ;
             this.notifySucceed(res.data.msg);

        })
        .catch(err => {
          return Promise.reject(err);
        });

    },
    // 加入购物车
    addShoppingCart() {
 
      // // 判断是否登录,没有登录则显示登录组件
      if (!this.$store.getters.getUser) {
        this.$store.dispatch("setShowLogin", true);
        return;
      }
      let as = this.$store.getters.getUserId;
      console.log(as);
      this.$axios( 
        { 
          url:this.$target+"/shoppingCart/addGood",
          // url:" http://localhost:3000/posts",
          method: 'post',
          params: {
              userId : as, 
              GoodsId: this.productID   
               }
      })
        .then(res => {
           
             this.notifySucceed(res.data.msg);
             
        })
        .catch(err => {
          return Promise.reject(err);
        });
    },
    addCollect() {
      // console.log("收藏");
      //  var productD=this.productID;
      //       productD["count"]=1
      // productD["check"]=false
      // this.unshiftCollent(productD);
      // 判断是否登录,没有登录则显示登录组件
      if (!this.$store.getters.getUser) {
        this.$store.dispatch("setShowLogin", true);
        return;
      }
      // this.$axios
      //   .get("http://localhost:3000/addcart", {
      //      product_id: this.productID
      //   })
      //   .then(res => {
      //     if (res.data.code == "001") {
      //       // 添加收藏成功
      //       this.notifySucceed(res.data.msg);
      //     } else {
      //       // 添加收藏失败
      //       this.notifyError(res.data.msg);
      //     }
      //   })
      //   .catch(err => {
      //     return Promise.reject(err);
      //   });
    }
  }
};
</script>
